.trainers-card {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  background-color: $color-link-water;

  @media (min-width: ($vp-1200)) {
    &:hover,
    &:focus,
    &:focus-within {
      .trainers-card__description {
        height: 100%;
        padding-bottom: 30px;
        background-color: rgba($color-sapphire, 0.89);
      }

      .trainers-card__hidden-description {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
  }

  @include focus {
    box-shadow: 0 0 0 5px $color-gamboge;
    outline: none;
  }
}

.trainers-card__image {
  box-sizing: border-box;
  grid-column: 1/2;
  grid-row: 1/2;
  width: 100%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.trainers-card__description {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  grid-column: 1/2;
  grid-row: 1/2;
  place-self: end center;
  display: grid;
  grid-template-rows: min-content;
  align-items: center;
  padding: 53px 30px 20px 30px;
  color: $color-default-white;
  overflow-y: scroll;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  @include vp-767 {
    padding: 42px 20px 20px 10px;
  }

  h3 {
    position: relative;
    z-index: 1;
    place-self: end center;
    margin: 0;
    padding: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    text-align: center;

    @include vp-1199 {
      transform: translate(5px, -2px);
    }
  }
}

.trainers-card__hidden-description {
  position: relative;
  z-index: 1;
  place-self: start center;
  display: none;
  width: 100%;

  p {
    margin: 0;
    margin-bottom: 28px;
    padding: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: $color-spindle;
    text-align: center;

    transform: translateY(-5px);

    @include vp-1199 {
      transform: translateY(-5px) translateX(-3px);
    }

    @include vp-767 {
      padding-left: 8px;
      transform: translateY(-4px);
      margin-bottom: 29px;
    }
  }

  ul {
    height: 120px;
    margin: 0;
    padding: 0;
    padding-left: 25px;
    list-style-type: none;
  }

  li {
    position: relative;
    margin: 0;
    margin-bottom: 13px;
    padding: 0;
    padding-left: 16px;
    font-size: 14px;
    line-height: 150%;

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 7px;
      display: block;
      width: 6px;
      height: 6px;
      background-color: $color-default-white;
      border-radius: 50%;
    }
  }
}

.trainers-card--active {
  .trainers-card__description {
    height: 100%;
    padding-bottom: 30px;
    background-color: rgba($color-sapphire, 0.89);
  }

  h3 {
    @include vp-1199 {
      transform: translate(-4px, -1px);
    }

    @include vp-767 {
      padding-left: 18px;
    }
  }

  .trainers-card__hidden-description {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
